<template>
  <img src="../../assets/exchange.png" alt="" class="background" />
  <h2 v-if="dataExchangeEUR != []" class="exchange__title">Курс EUR</h2>
  <div
    class="showData"
    ref="showData"
    @click="maxGraphEl"
    v-if="dataExchangeEUR != []"
  >
    <div
      class="graph"
      :style="{ height: `${item}` + 'px' }"
      v-for="item in dataExchangeEUR"
      :key="item"
    >
      {{ item.toFixed(1) }}
    </div>
  </div>
  <h2 v-if="dataExchangeEUR != []" class="exchange__title">Курс USD</h2>
  <div class="showData" v-if="dataExchangeUSD != []">
    <div
      class="graph"
      :style="{ height: `${item}` + 'px' }"
      v-for="item in dataExchangeUSD"
      :key="item"
    >
      {{ item.toFixed(1) }}
    </div>
  </div>
</template>

<script>
import {
  computed,
  getCurrentInstance,
  onBeforeUnmount,
  onMounted,
  ref,
  watch,
} from "@vue/runtime-core";
const { getEURData, getUSDData } = require("./exchange-node");

export default {
  emits: { triggerLoad: Boolean },
  setup(props, { emit }) {
    let triggerLoading = ref(true);
    let dataExchangeEUR = ref([]);
    let dataExchangeUSD = ref([]);
    let showData = ref(null);
    let graphs = ref([]);
    let instance = getCurrentInstance();
    function loadData() {
      emit("triggerLoad", triggerLoading.value);
      instance.appContext.config.globalProperties.$interval = setInterval(
        async () => {
          getUSDData().then((result) => {
            let resultRand =
              result.rates["RUB"] +
              (Math.random() + Math.floor(Math.random() * 11));
            dataExchangeUSD.value.push(resultRand);
          });
          getEURData().then((result) => {
            let resultRand =
              result.rates["RUB"] +
              (Math.random() + Math.floor(Math.random() * 11));
            dataExchangeEUR.value.push(resultRand);
          });
        },
        3000
      );
    }
    function clearInt() {
      clearInterval(instance.appContext.config.globalProperties.$interval);
    }
    let maxGraphEl = computed(() => {
      return showData.value.clientWidth / 30;
    });
    watch(dataExchangeEUR.value, () => {
      if (dataExchangeEUR.value.length > maxGraphEl.value) {
        dataExchangeEUR.value.shift();
      }
    });
    watch(dataExchangeUSD.value, () => {
      if (dataExchangeUSD.value.length > maxGraphEl.value) {
        dataExchangeUSD.value.shift();
      }
    });
    onMounted(loadData);
    onBeforeUnmount(clearInt);
    return {
      graphs,
      dataExchangeEUR,
      dataExchangeUSD,
      loadData,
      clearInt,
      showData,
      maxGraphEl,
    };
  },
};
</script>

<style scoped>
.background {
  pointer-events: none;
  z-index: 0;
  opacity: 0.3;
  width: 500px;
  height: 500px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.exchange__title {
  margin-top: 50px;
}
.showData {
  width: 320px;
  display: flex;
  align-items: flex-end;
  margin-bottom: 50px;
  margin: 0 auto;
  animation-name: blurre;
  animation-duration: 0.1s;
  animation-iteration-count: infinite;
}
@keyframes blurre {
  from {
    filter: blur(1.3px);
  }
  to {
    filter: blur(0px);
  }
}
.graph {
  border: 1px solid white;
  width: 30px;
  color: white;
  background-color: black;
}
.graph:not(:last-child) {
  /* margin-right: 20px; */
}
</style>
