const axios = require("axios");
let API_KEY = "be507d0c59804ab2b92133717220703";

function getData(currentCity, objectData){

axios
  .get(`https://api.weatherapi.com/v1/current.json?key=${API_KEY}&q=${currentCity.value}`)
  .then(res => {
    objectData.currentDegr = res.data.current["temp_c"];
    console.log("i am here");
    objectData.currentHumidity = res.data.current["humidity"];
    objectData.currentCondition = res.data.current.condition.text;
    objectData.windDirection = res.data.current["wind_dir"];
  })
  .catch(error => {
    console.error(error)
  })

}
module.exports = getData;



