<template>
  <img src="../../assets/weather.png" alt="" class="background" />
  <div class="map__wrapper">
    <img class="map__img" src="./../../assets/russia.png" alt="" />
    <div class="points">
      <div class="point" @click="loadData(city)" v-for="city in cities" :key="city">
        {{ city }}
      </div>
      <div class="modal" v-if="currentCity">
        {{ currentCity }}
        <ul class="info__list">
          <li class="info__item">
            Temperature: {{ objectData.currentDegr }}°C
          </li>
          <li class="info__item">
            Condition: {{ objectData.currentCondition }}
          </li>
          <li class="info__item">
            Humidity: {{ objectData.currentHumidity }}%
          </li>
          <li class="info__item">Wind: {{ objectData.windDirection }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, ref } from "@vue/runtime-core";
const getData = require("./weather-node");
export default {
  emits: { triggerLoad: Boolean },
  setup(props, { emit }) {
    let cities = ref([
      "Moscow",
      "Yekaterinburg",
      "Volgograd",
      "Saint-Petersburg",
    ]);
    let objectData = reactive({
      currentDegr: null,
      currentHumidity: null,
      currentCondition: null,
      windDirection: null,
    });
    let triggerLoading = ref(true);
    let currentCity = ref(null);
    function loadData(curCity) {
      emit("triggerLoad", triggerLoading.value);
      currentCity.value = curCity;
      getData(
        currentCity,
        objectData
      );
    }
    return {
      loadData,
      currentCity,
      objectData,
      cities,
      getData,
    };
  },
};
</script>

