const axios = require("axios");

const getUSDData = () => {
return axios
  .get(`https://api.exchangerate.host/latest?base=USD`)
  .then(res => {
    return res.data;
  }).catch(err => {
    console.log(err);
  })

}

const getEURData = () => {
    return axios
      .get(`https://api.exchangerate.host/latest?base=EUR`)
      .then(res => {
        return res.data;
      }).catch(err => {
        console.log(err);
      })
    
    }
module.exports = {
  getEURData,
  getUSDData,
}
